import React,{useState} from 'react'
import Champion from './Champion'
import Info from './Info'

const Main = () =>{
    const [activeRole, setActiveRole] = useState("All");
    const [activeChampion, setActiveChampion] = useState("Androxus");

    return(
        <div className="main">
            <div className="roleSelectionDiv">
                <div className={`roleSelection ${activeRole.toString() === "All" ? "active" :"" }`} onClick={()=> setActiveRole("All")}>
                    <h1>All</h1>
                </div>
                <div className={`roleSelection ${activeRole.toString() === "Damage" ? "active" :"" }`} onClick={()=> setActiveRole("Damage")}>
                    <img src="https://childishgiant.github.io/paladins-randomiser/imgs/Frontlines.svg" alt="Front Line" title="Front Line"/>
                    <h1>Damage</h1>
                </div>
                <div className={`roleSelection ${activeRole.toString() === "Front Line" ? "active" :"" }`} onClick={()=> setActiveRole("Front Line")}>
                    <img src="https://childishgiant.github.io/paladins-randomiser/imgs/Damage.svg" alt="Damage" title="Damage"/>
                    <h1>Front Line</h1>
                </div>
                <div className={`roleSelection ${activeRole.toString() === "Flank" ? "active" :"" }`} onClick={()=> setActiveRole("Flank")}>
                    <img src="https://childishgiant.github.io/paladins-randomiser/imgs/Flank.svg" alt="Flanker" title="Flanker"/>
                    <h1>Flank</h1>
                </div>
                <div className={`roleSelection ${activeRole.toString() === "Support" ? "active" :"" }`} onClick={()=> setActiveRole("Support")}>
                    <img src="https://childishgiant.github.io/paladins-randomiser/imgs/Support.svg" alt="Support" title="Support"/>
                    <h1>Support</h1>
                </div>
            </div>
            <div className="champions">
                <Champion name="Androxus" role="Flank" active={activeRole} setActiveChampion={setActiveChampion}/>
                <Champion name="Ash" role="Front Line" active={activeRole} setActiveChampion={setActiveChampion}/>
                <Champion name="Atlas" role="Front Line" active={activeRole} setActiveChampion={setActiveChampion}/>
                <Champion name="Barik" role="Front Line" active={activeRole} setActiveChampion={setActiveChampion}/>
                <Champion name="Bomb King" role="Damage" active={activeRole} setActiveChampion={setActiveChampion}/>
                <Champion name="Buck" role="Flank" active={activeRole} setActiveChampion={setActiveChampion}/>
                <Champion name="Cassie" role="Damage" active={activeRole} setActiveChampion={setActiveChampion}/>
                <Champion name="Corvus" role="Support" active={activeRole} setActiveChampion={setActiveChampion}/>
                <Champion name="Dredge" role="Damage" active={activeRole} setActiveChampion={setActiveChampion}/>
                <Champion name="Drogoz" role="Damage" active={activeRole} setActiveChampion={setActiveChampion}/>
            </div>
            <div className="championInfo">
                <Info activeChampion={activeChampion} setActiveChampion={setActiveChampion}/>    
            </div>
      </div>
    );
}

export default Main;