const Champion =(props, {setActiveChampion, activeChampion}) =>{
    return(
        <div className={`championCard ${props.active.toString() === "All" || props.active.toString() === props.role.toString() ? "" :"none" }`} onClick={()=> props.setActiveChampion(`${props.name}`)}>
            {console.log(props.active.toString())}
            <img src={`https://webcdn.hirezstudios.com/paladins/champion-icons/${props.name.toLowerCase().replace(/ /g, "-")}.jpg`} className="championIcon" alt={`${props.name}`} title={`${props.name}`}/>
            <h1>{props.name}</h1>
            <div className="description">
                {props.role.toString() === "Front Line" ? <img src={`https://childishgiant.github.io/paladins-randomiser/imgs/Frontlines.svg`} className="roleIcon" alt={`${props.role}`} title={`${props.role}`}/> : <img src={`https://childishgiant.github.io/paladins-randomiser/imgs/${props.role}.svg`} className="roleIcon" alt={`${props.role}`} title={`${props.role}`}/>} 
                <h2>{props.role}</h2>
            </div>
        </div>
    );
}

export default Champion;