import {FaDiscord, FaTwitter, FaFacebook, FaYoutube, FaInstagram, FaTwitch} from 'react-icons/fa'
const Socials = () => {
    return (
        <div className="socials">
            <a href="https://twitter.com/paladinsgame" target="_blank" rel="noreferrer"><FaTwitter className="socialIcon"/></a>
            <a href="https://facebook.com/paladinsgame" target="_blank" rel="noreferrer"><FaFacebook className="socialIcon" /></a>
            <a href="https://www.youtube.com/paladinsgame" target="_blank" rel="noreferrer"><FaYoutube className="socialIcon"/></a>
            <a href="https://instagram.com/paladinsgame" target="_blank" rel="noreferrer"><FaInstagram className="socialIcon"/></a>
            <a href="https://twitch.tv/paladinsgame" target="_blank" rel="noreferrer"><FaTwitch className="socialIcon"/></a>
            <a href="https://discord.gg/paladinsgame" target="_blank" rel="noreferrer"><FaDiscord className="socialIcon"/></a>
        </div>
    );
}

export default Socials;